<template>
  <div class="tv-stream-page">
    <el-button type="primary" size="mini" @click="fetchM3uList" style="margin-bottom:16px;"></el-button>
    <el-select v-model="selectedUrl" placeholder="" filterable style="width: 220px; margin-bottom: 24px">
      <el-option v-for="(item, index) in streamList" :key="index" :label="item.name" :value="item.url" />
    </el-select>
    <el-select v-model="selectedUrl" placeholder="FLV" filterable style="width: 220px; margin-bottom: 24px">
      <el-option v-for="(item, index) in streamListByFLV" :key="index" :label="item.name" :value="item.url" />
    </el-select>
    <div v-if="selectedUrl" class="player-area">
      <div id="dplayer" style="width: 800px; height: 450px; background: #000"></div>
    </div>
  </div>
</template>

<script>
import DPlayer from 'dplayer';
import Hls from 'hls.js';
import flvjs from 'flv.js'; //  flv.js

const STREAM_CACHE_KEY = 'tv_stream_list_cache';

export default {
  name: "TvStream",
  data() {
    return {
      streamList: [],
      selectedUrl: "",
      dp: null
    };
  },
  computed: {
    streamListByFLV() {
      return this.streamList.filter(item => item.url.endsWith('.flv') || item.url.endsWith('.hlv'));
    }
  },
  watch: {
    selectedUrl(newUrl) {
      if (newUrl) {
        this.initPlayer(newUrl);
      }
    }
  },
  mounted() {
    // 
    const cache = localStorage.getItem(STREAM_CACHE_KEY);
    if (cache) {
      try {
        this.streamList = JSON.parse(cache);
      } catch (e) {}
    }
    // 
    this.fetchM3uList();
  },
  methods: {
    async fetchM3uList() {
      try {
        const res = await fetch("https://gh.tryxd.cn/raw.githubusercontent.com/suxuang/myIPTV/main/ipv4.m3u");
        const text = await res.text();
        this.streamList = this.parseM3u(text);
        //  localStorage
        localStorage.setItem(STREAM_CACHE_KEY, JSON.stringify(this.streamList));
      } catch (e) {
        this.$message.error("");
      }
    },
    parseM3u(m3uText) {
      const lines = m3uText.split('\n');
      const list = [];
      let name = '';
      for (let i = 0; i < lines.length; i++) {
        const line = lines[i].trim();
        if (line.startsWith('#EXTINF')) {
          const match = line.match(/,(.*)$/);
          name = match ? match[1].trim() : '';
        } else if (line && !line.startsWith('#')) {
          list.push({ name, url: line });
        }
      }
      return list;
    },
    initPlayer(url) {
      console.log("URL:", url);
      if (this.dp) {
        this.dp.destroy();
        this.dp = null;
      }
      // 
      let type = 'auto';
      let customType = {};
      if (url.endsWith('.m3u8')) {
        console.log(" HLS  customHls");
        type = 'customHls';
        customType = {
          customHls: function(video) {
            if (Hls.isSupported()) {
              const hls = new Hls();
              hls.loadSource(url);
              hls.attachMedia(video);
            }
          }
        };
      } else if (url.endsWith('.flv') || url.endsWith('.hlv')) {
        console.log(" FLV  customFlv");
        type = 'customFlv';
        customType = {
          customFlv: function(video) {
            if (flvjs.isSupported()) {
              const flvPlayer = flvjs.createPlayer({
                type: 'flv',
                url: url
              });
              flvPlayer.attachMediaElement(video);
              flvPlayer.load();
              flvPlayer.play();
            }
          }
        };
      }
      this.$nextTick(() => {
        this.dp = new DPlayer({
          container: document.getElementById('dplayer'),
          autoplay: true,
          video: {
            url: url,
            type: type,
            customType: customType
          }
        });
        //  customType
        // if (type === 'customHls') {
        //   this.dp.switchVideo({
        //     url: url,
        //     type: 'customHls'
        //   });
        // } else if (type === 'customFlv') {
        //   this.dp.switchVideo({
        //     url: url,
        //     type: 'customFlv'
        //   });
        // }
      });
    }
  }
};
</script>

<style scoped>
.tv-stream-page {
  padding: 32px;
  background: #f8faff;
  min-height: 100vh;
}

.player-area {
  margin-top: 24px;
}
</style>